<template>
  <div class="project-step">
    <baseStep :steps="baseStepList" :curStep="curStep"></baseStep>
  </div>
</template>

<script type="text/javascript">
  import baseStep from './baseStep.vue'
  export default {
    name: 'project-step',
    props: {
        stepType:{
            type:Number,
            default:1
        },
        curStep:{
           default:''
        },
    },
    data() {
      return {
        setupSteps:[
            {value:'1',name:'',src:'static/img/stepicon/s_100_{0}.png'},
            {value:'1-1',name:'',type:'sm'},
            {value:'2',name:'',src:'static/img/stepicon/s_200_{0}.png'},
            {value:'3',name:'',src:'static/img/stepicon/s_300_{0}.png'},
            {value:'3-1',name:'',type:'sm'},
            {value:'4',name:'',src:'static/img/stepicon/s_400_{0}.png'}
          ],
        processSteps:[
            {value:'5',name:'',src:'static/img/stepicon/s_500_{0}.png'},
            {value:'6',name:'',  src:'static/img/stepicon/s_600_{0}.png'},
            {value:'7',name:'',       src:'static/img/stepicon/s_700_{0}.png'},
            {value:'8',name:'',       src:'static/img/stepicon/s_800_{0}.png'},
            {value:'9',name:'',       src:'static/img/stepicon/s_900_{0}.png'},
            {value:'10',name:'',      src:'static/img/stepicon/s_1000_{0}.png'},
            {value:'11',name:'',      src:'static/img/stepicon/s_1100_{0}.png'},
            {value:'12',name:'',      src:'static/img/stepicon/s_1200_{0}.png'},
        ],
        SimpleSetupSteps:[
            {value:'1',name:'',src:'static/img/stepicon/s_100_{0}.png'},
            {value:'2',name:'',src:'static/img/stepicon/s_200_{0}.png'},
            {value:'3',name:'',src:'static/img/stepicon/s_300_{0}.png'},
            {value:'4',name:'',src:'static/img/stepicon/s_400_{0}.png'}
        ],
        SimpleTenderSteps:[
            {value:'3',name:'',src:'static/img/stepicon/s_300_{0}.png'},
            {value:'3-1',name:'',type:'sm'},
            {value:'4',name:'',src:'static/img/stepicon/s_400_{0}.png'}
        ],
        baseStepList:[],
      }
    },
    mounted() {
      this.init();
    },
    methods: {
        init(){
           // 
           if(this.stepType == 1){
              this.baseStepList = JSON.parse(JSON.stringify(this.setupSteps));
           }
           else if (this.stepType == 2){
              this.baseStepList = JSON.parse(JSON.stringify(this.processSteps));
           }
           else if (this.stepType == 3){
              this.baseStepList = JSON.parse(JSON.stringify(this.SimpleSetupSteps));
           }
           else if (this.stepType == 4){
              this.baseStepList = JSON.parse(JSON.stringify(this.SimpleTenderSteps));
           }
           let findCurStep = false;
           for(var item of this.baseStepList){
              if(findCurStep ==  false){
                if(item.value === this.curStep){
                    findCurStep = true;
                    if(item.src&&item.src!=''){
                      item.src =  item.src.replace('{0}',1);
                    }
                }
                else{
                  if(item.src&&item.src!=''){
                    item.src =  item.src.replace('{0}',2);
                  }
                }
              }
              else{
                if(item.src&&item.src!=''){
                  item.src =  item.src.replace('{0}',0);
                }
              }
           }
          this.$set(this.baseStepList)
        }
    },
    components: {
      baseStep
    },
    watch:{
        curStep:function(){
          this.init();

        },
        stepType:function() {
          this.init();
          }
    },
  }
</script>

<style lang="scss" scoped>
  @import "../../../assets/css/variable";
  @import "../../../assets/css/mixin";
  .project-step{
    padding: 20px;
    background: #fff;
  }

</style>



