        })
      },
      openSuccess() {
        this.$message({
          message: '',
          type: 'success'
        })
      },
      openConfirm() {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`, this.curPageIndex);
      },
      resetPage() {
        this.curPageIndex = 1;
      }
    },
    components: {
      leftBar
    },
  }

</script>
